export default {
    name: 'index',
    data() {
        return {
            clickme: false
        }
    },
    methods: {
        toInputName() {
            this.$router.push('input-name')
        }
    },
    mounted() {
        const self = this
        setTimeout(() => {
            self.clickme = true
        }, 1000);
    }
}



